const axios = require('axios');
$(document).ready(function () {
    if (document.URL.match(/upload\/files/)) {
        let association_id = $('#association_id').val();
        if(association_id > 0) {
            $('.list-group-item').removeClass('active');
            let active_tab = $('#associations_list a[href="#association_' + association_id + '"]');
            active_tab.tab('show');
            document.getElementById(active_tab.attr('id')).scrollIntoView({ behavior: 'smooth', block: 'start', inline: 'nearest' });
        }
    }
    $('.list-group-item').keydown(function() {
        $('.list-group-item').removeClass('active');
    });
});
