import { form_elements, select_refresh } from '@/form_elements.js';
$(document).ready(function () {

    setTimeout(function() {
        $('.file-path').bind('change', function () {
            if ($('#file_name_display').val() == '') {
                let form_name = $(this).val().replace(/\.pdf/, '');
                $('#file_name_display').val(form_name).trigger('change');
            }
        });

    }, 500);

    setTimeout(function () {
        $('.form-select').each(function () {
            select_refresh($(this));
        });
    }, 200);

    $('#upload_file_form').submit(function (e) {
        e.preventDefault();
        $('#upload_file_button').prop('disabled', true).html('<span class="spinner-border spinner-border-sm"></span> Uploading');
        var formData = new FormData($(this)[0]);
        $.ajax({
            url: '/doc_management/upload_file',
            type: 'post',
            data: formData,
            processData: false,
            contentType: false,
            success: function (data) {
                window.location = '/create/upload/files/'+$('#association_id').val();
            }
        });
    });
});
